<template>
	<div class="news_container">
		<div class="mask" :show="!loaded">
			<div class="area">
				<div class="content">载入中，<br>请稍候……</div>
			</div>
		</div>
		<div class="title">{{title}}</div>
		<div class="stamp">{{stamp}}</div>
		<div ref="content" class="content"></div>
		<div class="backer"><a @click="backHome">&lt; 返回 &gt;</a></div>
	</div>
	<FootMark />
</template>

<style scoped>
.news_container {
}
.news_container > .mask {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 0%;
	background-color: rgb(185, 185, 195);
	opacity: 0;
	pointer-events: none;
	transition: opacity 300ms ease-in-out, height 300ms step-end;
}
.news_container > .mask[show="true"] {
	width: 100%;
	height: 100%;
	opacity: 1;
	pointer-events: auto;
}
.news_container > .mask > .area {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.news_container > .mask > .area > .content {
	font-size: 54px;
	color: rgb(235, 235, 235);
	text-shadow: 2px 2px 5px rgb(27, 27, 27);
}
.news_container > .title {
	padding-top: 30px;
	text-align: center;
	font-size: 36px;
	font-weight: bolder;
}
.news_container > .stamp {
	padding: 15px 10px 5px 10px;
	border-bottom: 1px solid rgb(205, 205, 205);
	font-size: 12px;
	color: rgb(175, 175, 175);
	text-align: right;
}
.news_container > .content {
	padding: 10px;
}
.news_container > .backer {
	margin-top: 30px;
	margin-bottom: 20px;
	font-size: 14px;
	text-align: center;
}
.news_container > .backer a {
	cursor: pointer;
}
</style>

<script>
import FootMark from '@/components/footmark.vue';

export default {
	name: 'News',
	components: {FootMark},
	data () {
		return {
			loaded: false,
			title: '',
			stamp: '',
		}
	},
	methods: {
		backHome () {
			this.$router.push({path: '/'});
		},
	},
	async mounted () {
		this.loaded = false;

		var {type, id} = this.$route.params;
		if (!type || !id) return;

		var news = await BackEnd.getNewsInfo(type, id);
		this.loaded = true;
		if (!news) {
			return;
		}

		this.title = news.title;
		this.$refs.content.innerHTML = news.content;
		this.stamp = stamp2string(news.stamp);
	},
	unmounted () {
	},
}
</script>